import React from "react"
import styled from "styled-components"
import { connect } from "react-redux"
import {
  toggle_modal,
  loadData,
  set_Delimiter,
  set_Decimal,
  set_Header,
  load_sample
} from "../../store/actions"
import { Select } from "../Inputs/select"
import { CheckBox } from "../Inputs/checkBox"
import { FileInput } from "../Inputs/fileInput"
import { Button } from "../Inputs/button"
import { DataTable } from "./table"
import { Close } from "./closeModal"

const Wrapper = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50;
  background-color: rgba(0, 0, 0, 0.85);
  padding: 50px 50px 0 50px;
  display: flex;
  flex-direction: column;
  animation: fadeIn 500ms ease;
`

const Container = styled.div`
  min-height: 70px;
  margin-bottom: 10px;
  position: relative;
  display: flex;
  align-items: center;
`

const Modal = ({
  toggleModal,
  readFile,
  loadSample,
  setHeader,
  setDelimiter,
  setDecimal,
  closed,
  columns,
  data
}) => {
  if (!closed) {
    return (
      <Wrapper>
        <Container>
          <FileInput handleChange={readFile}>Choose a file</FileInput>

          <Select
            options={[
              "Delimiter",
              "Comma",
              "empty space",
              "Colon",
              "Semicolon"
            ]}
            handleChange={setDelimiter}
          />

          <Select
            options={["Decimal", 'Option "."', 'Option ","']}
            handleChange={setDecimal}
          />

          <CheckBox handleClick={setHeader}>Has header</CheckBox>

          <Button
            handleClick={loadSample}
            style={{ backgroundColor: "#393E46", color: "#FBFBFB" }}
          >
            Import Example
          </Button>

          <Close onClick={toggleModal} />
        </Container>

        <DataTable data={data} columns={columns} />
      </Wrapper>
    )
  }
  return null
}

const state = ({ data }) => ({
  closed: data.closed,
  columns: data.columns,
  data: data.data
})

const actions = {
  toggleModal: toggle_modal,
  readFile: loadData,
  setHeader: set_Header,
  setDelimiter: set_Delimiter,
  setDecimal: set_Decimal,
  loadSample: load_sample
}

export default connect(state, actions)(Modal)



