import React from "react"
import styled from "styled-components"
import logo from "./logo.svg"

const Wrapper = styled.header`
  display: grid;
  grid-template-columns: 1fr minmax(300px, 1000px) 1fr;
  min-height: 55px;
  margin-bottom: 30px;
`

const Container = styled.div`
  grid-column: 2;
  display: flex;
  align-items: center;
`

const Brand = styled.div`
  display: flex;
  align-items: center;
  font-size: 1.6rem;
  color: #2c2e3e;

  > span {
    opacity: 0;
    transition: all 500ms ease;
    &:last-child {
      opacity: 1;
      transform: translateX(-94px);
    }
  }
  &:hover {
    > span {
      opacity: 1;
      &:last-child {
        transform: translateX(15px);
      }
    }
  }
`

const Author = styled.a.attrs({
  href: "https://carloseng.com",
  target: "_blank",
  rel: "noopener noreferrer"
})`
  text-decoration: none;
  font-size: 0.8rem;
  margin-left: 10px;
`

const Header = () => {
  return (
    <Wrapper>
      <Container>
        <Brand>
          <img
            style={{ width: 50, height: 50, marginRight: 10 }}
            src={logo}
            alt="controls chart online"
          />
          C<span>ontrol</span>
          <span>
            Chart online<Author>by Carlos Silva</Author>
          </span>
        </Brand>
      </Container>
    </Wrapper>
  )
}

export default Header



